import React, { useState, forwardRef, useImperativeHandle } from "react";
import ReactDOM from "react-dom";

const API = process.env.REACT_APP_API;

const Citashoy = forwardRef((props, ref) => {
  
    const [display, setDisplay] = useState(false);
    const [name, setName] = useState(false);
    const [citas, setCitas] = useState(false);

    useImperativeHandle(ref, () => {
        return {
          openHoy: (barbero, id) => open(barbero, id),
          closecitas: () => close(),
        };
      });
  
    const open = async (barbero, id) => {
      setName(barbero)
      const res = await fetch(`${API}/citasHoy/${id}`);
      const data = await res.json();
      setCitas(data);
      setDisplay(true);
    };
  
    const close = () => {
      setDisplay(false);
    };
  
  
    if (display) {
      return ReactDOM.createPortal(
        <div className="modal-wrapper">
          <div className="modal-backdrop" />
          <div className="modal-box">
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title"> {name}</h5>
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                    onClick={close}
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div className="modal-body">
                  {props.children}
                  <table class="table table-striped table-primary">
  <thead>
    <tr>
      <th scope="col"><h5>Cliente</h5></th>
      <th scope="col"><h5>Hora</h5></th>
      <th scope="col"><h5>Día</h5></th>
    </tr>
  </thead>
  <tbody>
    {citas.map((cita) => (
      <tr key={cita._id}>
      <td>{cita.cliente}</td>
    <td>{cita.hora}</td>
    <td>{cita.fecha}</td>
    </tr>
    ))}
  </tbody>
</table> 
                </div>
                <div className="modal-footer">
                <button type="button" className="btn btn-danger disabled" onClick={close}>
                  Cerrar
                </button>
                </div>
              </div>
            </div>
          </div>
        </div>,
        document.getElementById("modal-citas")
      );
    }
  
    return null;
  });
  
  export default Citashoy;
  