import React, { useState, useEffect, useRef } from "react";
import Modal from './Modalcita'
import Modalcitas from './Modalcitas'
import Citashoy from './Citashoy'

const API = process.env.REACT_APP_API;

export const Barbers = () => {
  
  const modalRef = useRef();
  const modalCitas = useRef();
  const modalHoy = useRef();
  const nameInput = useRef(null);
  
  const [name, setName] = useState("");
  let [barberos, setBarberos] = useState([]);

  const handleSubmit = async (e) => {
    e.preventDefault();
      const res = await fetch(`${API}/barberos`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          name
        }),
      });
      await res.json();
    await getUsers();

    setName("");
    nameInput.current.focus();
  };

  const getUsers = async () => {
    const res = await fetch(`${API}/barberos`);
    const data = await res.json();
    setBarberos(data);
  };


  const deleteBarber = async (id) => {
    const userResponse = window.confirm("Estas seguro de eliminar al barbero?");
    if (userResponse) {
      const res = await fetch(`${API}/barberos/${id}`, {
        method: "DELETE",
      });
      const data = await res.json();
      console.log(data);
      await getUsers();
    }
  };
  
  const openModal = (barbero, id) => {
    modalRef.current.openModal(barbero, id)
  }

  const openModalcitas = (barbero, id) => {
    modalCitas.current.openModalcitas(barbero, id)
  }

  const openHoy = (barbero, id) => {
    modalHoy.current.openHoy(barbero, id)
  }

  useEffect(() => {
    getUsers();
  }, []);




  return (
    <div className="row row-cols-1 row-cols-md-3">

        {barberos.map((barbero)=>(
              <div className="col mb-4" key={barbero._id}>
              <div className="card bg-primary">
                <img
                  className="card-img-top maxImage"
                  src="img_avatar1.png"
                  alt="Card image"
                />
                <div className="card-body">
                  <h4 className="card-title">{barbero.barber}</h4>
                  <div className="btn-group mb-3">
                  <button type="button" className="btn btn-secondary mr-2" onClick={()=>openHoy(barbero.barber, barbero._id)}>Citas para hoy</button>
                  <button type="button" className="btn btn-outline-warning" onClick={()=> openModalcitas(barbero.barber, barbero._id)}>Ver todas las citas</button>
                  </div>
                  <button type="button" className="btn btn-danger btn-lg btn-block" onClick={()=>openModal(barbero.barber, barbero._id)}>Agendar Cita</button>
                </div>
                <button type="button" className="close" data-dismiss="alert" onClick={(e) => deleteBarber(barbero._id)}>&times;</button>
              </div>
              </div>
        ))}




        <div className="col mb-4">
      <div className="card bg-primary">
        <img
          className="card-img-top maxImage"
          src="img_avatar1.png"
          alt="Card image"
        />
        <div className="card-body">
          <h4 className="card-title">Agregar nuevo barbero</h4>
          <input
                  type="text"
                  onChange={(e) => setName(e.target.value)}
                  value = {name}
                  className="form-control"
                  placeholder="Nombre"
                  ref={nameInput}
                  autoFocus
                /><br/>
          <button type="button" className="btn btn-danger btn-lg btn-block" onClick={(e) => handleSubmit(e)}>Registrar</button>
        </div>
      </div>
      </div>
      <Modal ref={modalRef}/>
      <Modalcitas ref={modalCitas}/>
      <Citashoy ref={modalHoy}/>
    </div>
  );
};
