import React, { useState, forwardRef, useImperativeHandle, useRef } from "react";
import ReactDOM from "react-dom";
import MaterialUIPickers from "./Datehour"

const API = process.env.REACT_APP_API;

const Modal = forwardRef((props, ref) => {
  

  const fechaInput = useRef()
  const nameInput = useRef(null);

  const [display, setDisplay] = useState(false);
  const [name, setName] = useState("");
  const [id, setId] = useState("");
  const [cliente, setCliente] = useState("");




  useImperativeHandle(ref, () => {
    return {
      openModal: (barbero, id) => open(barbero, id),
      close: () => close(),
    };
  });

  const open = (barbero, id) => {
    setName(barbero)
    setId(id)
    setDisplay(true);
  };

  const close = () => {
    setCliente("")
    setDisplay(false);
  };

  const agendarCita = async () => {
    const res = await fetch(`${API}/citas`, {
      method: "POST",
      headers: {
        "Content-Type":"application/json",
      },
      body: JSON.stringify({
        id,
        fecha: fechaInput.current.fecha,
        cliente: cliente
      })
    });
    const message = await res.json();
    close();
  }

  

  if (display) {
    return ReactDOM.createPortal(
      <div className="modal-wrapper">
        <div className="modal-backdrop" />
        <div className="modal-box">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
    <h5 className="modal-title"> {name}</h5>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-label="Close"
                  onClick={close}
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
              <input
                  type="text"
                  onChange={(e) => setCliente(e.target.value)}
                  value={cliente}
                  className="form-control"
                  placeholder="Nombre del cliente"
                  ref={nameInput}
                  autoFocus
                /><br/>
                {props.children}
                <MaterialUIPickers ref={fechaInput}/> 
                
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-primary" onClick={agendarCita}>
                  Agendar
                </button>
                <button type="button" className="btn btn-danger disabled" onClick={close}>
                  Cancelar
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>,
      document.getElementById("modal-root")
    );
  }

  return null;
});

export default Modal;
